.transactionHistory {
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  width: 900px;
  box-shadow: 0px 5px 5px 1px black;
  border-radius: 8px;
  margin-bottom: 20px;
}

.transactionHistory thead th {
  height: 50px;
  text-transform: uppercase;
  color: #fff;
  background-color: lightseagreen;
}

.transactionHistory td {
  padding: 7px 10px 7px 10px;
  text-align: center;
  text-transform: capitalize;
  color: lightslategray;
  /* outline: 0.1px solid rgb(243, 237, 237); */
}

.transactionHistory tbody tr {
  background-color: rgb(231, 231, 231);
}

.transactionHistory tbody tr:nth-of-type(2n + 1) {
  background-color: #fff;
}
